import { useEffect } from "react";
import { useDispatch } from "react-redux";
import "./App.scss";
import "./scss/themes.scss";

import ThemeAction from "./redux/actions/ThemesAction";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import { Sidebar } from "./components/Sidebar";
import { Dashboard } from "./components/Dashboard";
import { Customers } from "./components/Customers";
import { TopNav } from "./components/TopNav";
import { useSelector } from "react-redux";

function App() {
  const themeAction = useSelector((state) => state.ThemeReducer);
  const dispatch = useDispatch();
  useEffect(() => {
    const themeClass = localStorage.getItem("themeMode", "theme-mode-light");

    const colorClass = localStorage.getItem("colorMode", "theme-mode-light");

    dispatch(ThemeAction.setMode(themeClass));

    dispatch(ThemeAction.setColor(colorClass));
  }, [dispatch]);
  return (
    <Router>
      <div className="App ">
        <div className={`layout ${themeAction.mode} ${themeAction.color}`}>
          <Sidebar />
          <div className="layout__content">
            <TopNav />
            <Switch>
              <Route path="/" exact component={Dashboard} />
              <Route path="/data" exact component={Customers} />
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;
