import React from "react";
import { Link } from "react-router-dom";
export const SidebarItem = ({ items, current, change }) => {
  const hanldeActive = (name) => {
    change(name);
  };
  return (
    <div className="sidebar__item">
      <div className="sidebar__item-iners">
        {items.map((item, index) => {
          return (
            <Link to={item.route} key={index}>
              <div
                className={
                  item.display_name === current
                    ? "sidebar__item-iner active"
                    : "sidebar__item-iner"
                }
                onClick={() => {
                  hanldeActive(item.display_name);
                }}
              >
                <i className={item.icon}></i>
                <span className="txt">{item.display_name}</span>
              </div>
            </Link>
          );
        })}
      </div>
    </div>
  );
};
