import React from "react";
import { useSelector } from "react-redux";
import "../scss/Dashboard.scss";
import { Grid } from "@material-ui/core";
import Chart from "react-apexcharts";
import status from "../assets/Status";
import topCustomers from "../assets/TopCustomers";
import { Table } from "./Table";
import { latestOrders } from "../assets/Oders";
export const Dashboard = () => {
  const themeReducer = useSelector((state) => state.ThemeReducer.mode);
  const chartOptions = {
    series: [
      {
        name: "Online Customers",
        data: [40, 70, 20, 90, 36, 80, 30, 91, 60],
      },
      {
        name: "Store Customers",
        data: [40, 30, 70, 80, 40, 16, 40, 20, 51, 10],
      },
    ],
    options: {
      color: ["#6ab04c", "#2980b9"],
      chart: {
        background: "transparent",
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        curve: "smooth",
      },
      xaxis: {
        categories: [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sep",
        ],
      },
      legend: {
        position: "top",
      },
      grid: {
        show: false,
      },
    },
  };

  const renderCustomerHead = (item, index) => {
    return <th key={index}>{item}</th>;
  };

  const renderCusomerBody = (item, index) => (
    <tr key={index}>
      <td>{item.username}</td>
      <td>{item.order}</td>
      <td>{item.price}</td>
    </tr>
  );

  const renderOrderHead = (item, index) => <th key={index}>{item}</th>;

  const renderOrderBody = (item, index) => (
    <tr key={index}>
      <td>{item.id}</td>
      <td>{item.user}</td>
      <td>{item.price}</td>
      <td>{item.date}</td>
    </tr>
  );
  return (
    <div className="dashboard">
      <h2 className="dashboard__title">Dashboard</h2>
      <div className="dashboard__content">
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12} md={6}>
            <div className="dashboard__content-status">
              {status.map((item, index) => {
                return (
                  <div className="status__item" key={index}>
                    <i className={`status__item-icon ${item.icon}`}></i>
                    <div className="status__item-info">
                      <p className="count">{item.count}</p>
                      <p className="title">{item.title}</p>
                    </div>
                  </div>
                );
              })}
            </div>
          </Grid>
          <Grid item xs={12} sm={12} md={6}>
            <div className="dashboard__content-chart">
              <Chart
                options={
                  themeReducer === "theme-mode-dark"
                    ? {
                        ...chartOptions.options,
                        theme: { mode: "dark" },
                      }
                    : {
                        ...chartOptions.options,
                        theme: { mode: "light" },
                      }
                }
                series={chartOptions.series}
                type="line"
                height="100%"
              />
            </div>
          </Grid>
          <Grid item xs={12} sm={12} md={4}>
            <div className="dashboard__content-customers">
              <h3>Tops Customers</h3>
              <Table
                headData={topCustomers.head}
                renderHead={(item, index) => renderCustomerHead(item, index)}
                bodyData={topCustomers.body}
                renderBody={(item, index) => renderCusomerBody(item, index)}
              />
            </div>
          </Grid>
          <Grid item xs={12} sm={12} md={8}>
            <div className="dashboard__content-orders">
              <h3>Laster Orders</h3>
              <Table
                headData={latestOrders.header}
                renderHead={(item, index) => renderOrderHead(item, index)}
                bodyData={latestOrders.body}
                renderBody={(item, index) => renderOrderBody(item, index)}
              />
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};
